a,img,button,input,textarea{-webkit-tap-highlight-color:rgba(255,255,255,0);}

.box{
  -webkit-user-select: none;/*禁用手机浏览器的用户选择功能 */  
  -moz-user-select: none;
  height: 63px;
  margin: auto;
  width: 180px;

  .inputbox{
    border:1px solid #e8e8e8;
    border-radius:4px;
    width:48px;
    height:48px;
    line-height: 48px;
    margin-right: 10px;
    text-align: center;
    outline:none;
    -webkit-appearance: none;
    float: left;
  }

  .inputBtn{
    border:1px solid #e8e8e8;
    border-radius:4px;
    width:48px;
    height:48px;
    margin-right: 10px;
    text-align: center;
    outline:none;
    -webkit-appearance: none;
  }

  .select{
    border:1px solid #ff7739;
  }

}
 .ver-btn{
    background:#cecece;
    border-radius:2px;
    width:125px;
    height:30px;
    line-height: 30px;
    margin: 5px auto;
    color: #ffffff;
    font-family:'PingFangSC-Regular';
    font-size:14px;
    text-align: center;
  }
  .click_btn{
    background:#ff7739;
  }

  .inputTruebox{
    border: none;
    outline: none;
    background-color: rgba(0,0,0,0);
    height: 0;
    width:0;
    color:rgba(0,0,0,0);
    -webkit-appearance: none;
    opacity: 0;
    text-indent: -9999px; // 隐藏input文字
    margin-left: -300%;// 隐藏input光标
  }


.bar{
  width:800px;
  height:400px;
  border-bottom: 1px solid #000;
  border-left:1px solid #000;
  position: relative;
  left:100px;


}

.inner{
  width:50px;
  position: absolute;
  bottom: 0;
  background-color: red;
}





